import React from 'react';
import { Form, Input } from 'antd';
import { FormComponentProps } from 'antd/lib/form';

const { TextArea } = Input;

interface Props<T = any> extends FormComponentProps {
  hasReason?: boolean;
  dataSource: T;
}

function ConfirmContent(props: Props) {
  const {
    hasReason,
    dataSource,
    form: { getFieldDecorator }
  } = props;

  return (
    <>
      <p>{dataSource.ordercode}</p>
      <p>{dataSource.amount}</p>
      {hasReason ? (
        <Form layout="inline" labelCol={{ span: 4 }} wrapperCol={{ span: 20 }}>
          <Form.Item label="" style={{ width: '100%' }}>
            {getFieldDecorator('reason', { rules: [{ required: true, message: '' }] })(
              <TextArea placeholder="" autosize={{ minRows: 4, maxRows: 8 }} />
            )}
          </Form.Item>
        </Form>
      ) : null}
    </>
  );
}

export default Form.create<Props>({ name: 'reason' })(ConfirmContent);
