import React, { useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect, RouteComponentProps } from 'react-router-dom';
import { LocaleProvider } from 'antd';
import moment from 'moment';
import 'moment/locale/zh-cn';
import zhCN from 'antd/es/locale-provider/zh_CN';
// import CmsLayout1 from '../layout/CmsLayout1';
// pages
import Search from '../pages/order/Search';
import Detail from '../pages/order/Detail';

// tslint:disable-next-line: no-var-requires
const OceanMessage = require('@ocean/message').default;

// console.log(Ocean);

moment.locale('zh-cn');

const Pages = (
  <Switch>
    <Redirect from="/" to="/byj" exact />
    <Route path="/byj" component={ByjMain} />
  </Switch>
);

function ByjMain(props: RouteComponentProps) {
  const { match, history } = props;

  useEffect(() => {
    OceanMessage.MsgTrigger('EVENT:SYSTEM_MOUNT', history);
    console.log('SYSTEM_MOUNT, mount');
  }, []);

  return (
    <Switch>
      <Route path={`${match.url}/order`} component={Search} exact />
      <Route path={`${match.url}/order/:code`} component={Detail} />
      <Redirect to={`${match.url}/order`} />
    </Switch>
  );
}

export default function App() {
  return (
    <LocaleProvider locale={zhCN}>
      <Router>
        {/* <CmsLayout1 routes={[{ name: '', icon: 'user', key: '/byjorder', path: '/byjorder' }]}>{Pages}</CmsLayout1> */}
        {Pages}
      </Router>
    </LocaleProvider>
  );
}
