import React, { Component } from 'react';
import { Form, Row, Col, Input, Button, Select, DatePicker, Checkbox } from 'antd';
import { WrappedFormInternalProps, FormProps } from 'antd/lib/form/Form';
import styles from './SearchForm.module.scss';

const { Option } = Select;
const { RangePicker } = DatePicker;

interface Props extends WrappedFormInternalProps {
  onSubmit?: FormProps['onSubmit'];
}

class SearchForm extends Component<Props, any> {
  handleSearch: FormProps['onSubmit'] = e => {
    const { form, onSubmit } = this.props;
    e.preventDefault();
    form.validateFields((err, values) => {
      console.log('Received values of form: ', values, err);
      if (typeof onSubmit === 'function') {
        onSubmit(e);
      }
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const {
      form: { getFieldDecorator }
    } = this.props;

    return (
      <Form className="ant-advanced-search-form" onSubmit={this.handleSearch} layout="vertical" colon>
        <Row gutter={24}>
          <Col span={8} key="ordercode">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('ordercode')(<Input placeholder="/" allowClear />)}
            </Form.Item>
          </Col>
          <Col span={8} key="name">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('name')(<Input placeholder="" allowClear />)}
            </Form.Item>
          </Col>
          <Col span={8} key="mobile">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('mobile')(<Input placeholder="" allowClear type="phone" maxLength={11} />)}
            </Form.Item>
          </Col>
          <Col span={8} key="zjtd">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('zjtd', { initialValue: '0' })(
                <Select>
                  <Option value="0"></Option>
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8} key="kq">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('kq', { initialValue: '0' })(
                <Select>
                  <Option value="0"></Option>
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                  <Option value="3"></Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8} key="fjyh">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('fjyh', { initialValue: '0' })(
                <Select>
                  <Option value="0"></Option>
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8} key="ddzt">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('ddzt', { initialValue: '0' })(
                <Select>
                  <Option value="0"></Option>
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                  <Option value="3"></Option>
                  <Option value="4"></Option>
                  <Option value="5"></Option>
                  <Option value="6"></Option>
                  <Option value="7"></Option>
                  <Option value="8"></Option>
                  <Option value="9"></Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8} key="ddcjsj">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('ddcjsj')(<RangePicker style={{ width: '100%' }} />)}
            </Form.Item>
          </Col>
          <Col span={8} key="ddfksj">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('ddfksj')(<RangePicker style={{ width: '100%' }} />)}
            </Form.Item>
          </Col>
          <Col span={24} key="kssx">
            <Form.Item label="" className={styles.item}>
              {getFieldDecorator('kssx')(
                <Checkbox.Group style={{ width: '100%' }}>
                  <Checkbox value="A"></Checkbox>
                  <Checkbox value="B"></Checkbox>
                </Checkbox.Group>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col style={{ textAlign: 'right' }}>
            <Button type="primary" htmlType="submit">
              
            </Button>
            <Button style={{ marginLeft: 8 }} onClick={this.handleReset}>
              
            </Button>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default Form.create<Props>({ name: 'order-search-form' })(SearchForm);
